.fa{margin-right:14px;display:inline-block;font-family:"Font Awesome 5 Free";font-size:14px;font-style:normal;font-variant:normal;line-height:1;color:rgba(0,0,0,0);text-rendering:auto;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:-webkit-gradient(linear, left bottom, left top, from(#f26522), color-stop(99%, #fa0891));background:linear-gradient(to top, #f26522, #fa0891 99%);-webkit-background-clip:text;background-clip:text;width:20px}.fa--phone{color:rgba(0,0,0,0);-webkit-transform:rotate(90deg);transform:rotate(90deg);background:-webkit-gradient(linear, left top, left bottom, from(#f26522), color-stop(99%, #fa0891));background:linear-gradient(to bottom, #f26522, #fa0891 99%);-webkit-background-clip:text;background-clip:text}.fa--phone::before{content:""}.fa--envelope:before{content:""}.fa--user:before{content:""}.fa--link:before{content:""}@media(max-width: 1023px)and (min-width: 950px){.fa--phone{font-size:18px}.fa--envelope{font-size:18px}.fa--user{font-size:18px}.fa--link{font-size:18px}}@media(min-width: 1330px){.fa--phone{font-size:20px}.fa--envelope{font-size:20px}.fa--user{font-size:20px}.fa--link{font-size:20px}}