.fa {
  margin-right: 14px;
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  line-height: 1;
  color: transparent;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to top, #f26522, #fa0891 99%);
  -webkit-background-clip: text;
          background-clip: text;
  width: 20px;
}
.fa--phone {
  color: transparent;
  transform: rotate(90deg);
  background: linear-gradient(to bottom, #f26522, #fa0891 99%);
  -webkit-background-clip: text;
          background-clip: text;
}
.fa--phone::before {
  content: "\f095";
}
.fa--envelope:before {
  content: "\f0e0";
}
.fa--user:before {
  content: "\f007";
}
.fa--link:before {
  content: "\f0c1";
}

@media (max-width: 1023px) and (min-width: 950px) {
  .fa--phone {
    font-size: 18px;
  }
  .fa--envelope {
    font-size: 18px;
  }
  .fa--user {
    font-size: 18px;
  }
  .fa--link {
    font-size: 18px;
  }
}
@media (min-width: 1330px) {
  .fa--phone {
    font-size: 20px;
  }
  .fa--envelope {
    font-size: 20px;
  }
  .fa--user {
    font-size: 20px;
  }
  .fa--link {
    font-size: 20px;
  }
}